import React, {Component} from "react";
import {connect} from "react-redux";
import RNS, {ADDRESS_ZERO} from "../../lib/rns/rns";

class TokenDetailContainer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            nodeAddress : undefined
        };
        console.log(this.props.selectedSuggestion);
    }

    async componentDidMount() {
        const rnsResolver = RNS.getRnsResolver();
        let addr = await rnsResolver.addr(process.env.REACT_APP_NODE_RNS_DOMAIN);
        if(addr === ADDRESS_ZERO){
            // Fallback for when is not registered
            addr = process.env.REACT_APP_NODE_ADDRESS;
        }
        this.setState({nodeAddress: addr })
    }

    render=()=> {
        return <div className="container-fluid p-5">
            <div className="row mb-4">
                <div className="col text-center">
                    <h5 className="name text-green w-600 m-0">{process.env.REACT_APP_NODE_RNS_DOMAIN}</h5>
                </div>
            </div>
            <div className="row mb-4">
                <div className="col text-center">

                </div>
            </div>
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        selectedSuggestion: state.searchReducer.suggestion.value,
    }
};

export default connect(mapStateToProps)(TokenDetailContainer);