import React, {Component} from "react";
import { ReactComponent as Logo } from './logo-rif.svg';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import SearchSuggestion from "../../components/search/SearchSuggestion";
import {quickPayment} from "../../actions/paymentsActions";

import {onChange, selectedSuggestion} from "../../actions/searchActions";
import {onSuggestionsFetchRequested} from "../../actions/searchActions";
import {onSuggestionsClearRequested} from "../../actions/searchActions";
import {loadSuggestionsBegin} from "../../actions/searchActions";

import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import axios from "axios";
import {mapSuggestions} from "../../actions/actionUtils";
import {TOKEN} from "../../lib/search/searchConstants";
import RNS, {ADDRESS_ZERO} from "../../lib/rns/rns";
import {getName} from "../../lib/tokens/tokensLogic";

import {tableColumns} from "../../apiModels/tableModelTasks";
import TasksTable from "../../components/tables/TasksTable";

class HeaderContainer extends Component {
    constructor(props, context) {
        super(props, context);

        this.state = {
            nodeAddress : undefined,
            show: false,
            showPendingTasks: false,
            partnerSuggestions: [],
            inputAddress: "",
            tokensSuggestions: [],
            inputToken: ""

        };

        this.handleShow = this.handleShow.bind(this);
        this.handleShowPendingTasks = this.handleShowPendingTasks.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.handleClosePendingTasks = this.handleClosePendingTasks.bind(this);
        this.handleChangeAddress = this.handleChangeAddress.bind(this);
        this.handleChangeToken = this.handleChangeToken.bind(this);
        this.handleChangeAmount = this.handleChangeAmount.bind(this);
        this.handleShowNodeDetail = this.handleShowNodeDetail.bind(this);
        this.handleCloseNodeDetail = this.handleCloseNodeDetail.bind(this);
    }

    async componentDidMount() {
        let addr="";
        try {
            const rnsResolver = RNS.getRnsResolver();
            addr = await rnsResolver.addr(process.env.REACT_APP_NODE_RNS_DOMAIN);
            if(addr === ADDRESS_ZERO){
                // Fallback for when is not registered
                addr = process.env.REACT_APP_NODE_ADDRESS;
            }
        }catch (e) {
            //Fallback for errors (no rns for example)
            addr = process.env.REACT_APP_NODE_ADDRESS;
        }
        this.setState({nodeAddress: addr });
    }

    handleClose() {
        this.setState({ show: false });
    }

    handleShow() {
        this.setState({ show: true });
    }

    handleClosePendingTasks() {
        this.setState({ showPendingTasks: false });
    }

    handleShowPendingTasks() {
        this.setState({ showPendingTasks: true});
    }

    handleCloseNodeDetail() {
        this.setState({ showNodeDetail: false });
    }

    handleShowNodeDetail() {
        this.setState({ showNodeDetail: true});
    }

    handleChangeAddress=(event, { newValue }) => {
        this.setState({
            inputAddress: typeof newValue !== 'undefined' ? newValue : '',
        });
    }

    handleChangeToken=(event, { newValue }) => {
        this.setState({inputToken: typeof newValue !== 'undefined' ? newValue : '',});
    }

    handleChangeAmount(event) {
        this.setState({inputAmount: event.target.value});
    }

    handleQuickPayments=()=>{
        this.props.quickPayment(
            this.state.inputAddress,
            this.state.inputToken,
            this.state.inputAmount,
        );
        this.handleClose();
    };

    getTokensSuggestions = (value) =>{
        if(value!=null && value!==""){
            let suggestions =[];
            if(this.props.tokens && this.props.tokens.length > 0) {
                for(let i=0; i< this.props.tokens.length; i++){
                    if(this.props.tokens[i].address.toLowerCase().includes(value.toLowerCase())){
                        suggestions.push({value: this.props.tokens[i].address, type: TOKEN, label: getName(this.props.tokens[i].address, this.props.tokens) + " - "+this.props.tokens[i].address})
                    }
                }
            }
            this.setState({tokensSuggestions:suggestions})
        }
    };

    fetchPaymentSuggestions = (value) =>
        new Promise((resolve, reject) =>
            axios.get("http://127.0.0.1:5001/api/v1/searchLumino", {
                params: {
                    query: value,
                    only_receivers: true
                }
            }).then(response => {
                console.log(JSON.stringify(response.data));
                if (response) {
                    let mappedResponse = mapSuggestions(response.data);
                    this.setState({partnerSuggestions: mappedResponse});
                }
            }).catch(error => {
                console.log(JSON.stringify(error));
            })
        );

    onPartnerSelected =(event, { suggestion}) => {
        this.setState({inputAddress: suggestion.value})
    };

    onTokenSelected =(event, { suggestion}) => {
        this.setState({inputToken: suggestion.value})
    };

    onPartnerSuggestionsClearRequested = () => {
        this.setState({
            partnerSuggestions: []
        });
    };

    onTokenSuggestionsClearRequested = () => {
        this.setState({
            tokensSuggestions: []
        });
    };

    render =()=> {

        let tableTasks = null;

        if (this.props.tasks) {
            tableTasks = <TasksTable data={this.props.tasks} columns={tableColumns} defaultPageSize={5} showPagination={false} sortable={false} />;
        }

        return <header className="shadow-sm position-fixed w-100">
            <nav className="navbar navbar-expand-lg navbar-light bg-white p-0">
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#side-bar"
                        aria-controls="side-bar" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <a className="navbar-brand mx-auto ml-lg-3 mr-lg-0 py-0" href="/">
                    <Logo />
                </a>
                <ul className="navbar-nav ml-4 pl-4 border-left">
                    <li className="address-header">
                        <span className="name text-green w-600">{process.env.REACT_APP_NODE_RNS_DOMAIN}</span>
                        <small className="address d-block">{this.state.nodeAddress}</small>
                    </li>
                </ul>

                <ul className="navbar-nav ml-lg-auto flex-row align-items-center">
                    <li className="nav-item">
                        <SearchSuggestion
                            placeholder={'Search...'}
                            selectedSuggestion ={this.props.selectedSuggestion}
                            redirect={true}
                            suggestions={this.props.suggestions}
                            value={this.props.value}
                            isLoading={this.props.isLoading}
                            noSuggestions={this.props.noSuggestions}
                            onSuggestionsClearRequested={this.props.onSuggestionsClearRequested}
                            onSuggestionsFetchRequested={this.props.onSuggestionsFetchRequested}
                            onChange={this.props.onChange}
                            loadSuggestionsBegin={this.props.loadSuggestionsBegin}
                        />
                    </li>
                    <li className="nav-item">
                        <button type="button" name="button" className="nav-link btn-search px-3">
                            <i className="fal fa-search"></i>
                        </button>
                    </li>
                    <li className="nav-item">
                        <button type="button" name="button" className="nav-link border-0 bg-transparent px-3 text-green" onClick={this.handleShow}>
                            <i className="fal fa-money-bill"></i>
                        </button>
                    </li>
                    <li className="nav-item">
                        <button type="button" name="button" className="nav-link border-0 bg-transparent px-3 text-blue position-relative btn-bell" onClick={this.handleShowPendingTasks}>
                            <i className="fal fa-bell"></i> <span className="badge badge-dark position-absolute"> {this.props.tasksCount} </span>
                        </button>
                    </li>
                </ul>
            </nav>

            <Modal show={this.state.showPendingTasks} onHide={this.handleClosePendingTasks} centered>
                <Modal.Header closeButton>
                    <Modal.Title>Pending Tasks</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {tableTasks}
                </Modal.Body>
                <Modal.Footer>
                </Modal.Footer>
            </Modal>

            <Modal show={this.state.show} onHide={this.handleClose} centered>
                <Modal.Header closeButton>
                    <Modal.Title>Quick Payment</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="form-group">
                        <SearchSuggestion
                            placeholder={'Partner address'}
                            suggestions={this.state.partnerSuggestions}
                            value={this.state.inputAddress}
                            onSuggestionsFetchRequested={this.fetchPaymentSuggestions}
                            onSuggestionsClearRequested={this.onPartnerSuggestionsClearRequested}
                            onChange={this.handleChangeAddress}
                            onSuggestionSelected={this.onPartnerSelected}
                        />
                    </div>
                    <div className="form-group">
                        <SearchSuggestion
                            placeholder={'Token address'}
                            suggestions={this.state.tokensSuggestions}
                            value={this.state.inputToken}
                            onSuggestionsFetchRequested={this.getTokensSuggestions}
                            onSuggestionsClearRequested={this.onTokenSuggestionsClearRequested}
                            onChange={this.handleChangeToken}
                            onSuggestionSelected={this.onTokenSelected}
                        />
                    </div>
                    <div className="form-group">
                        <input type="text" className="form-control shadow-none" id="" aria-describedby="" placeholder="Amount:" value={this.state.inputAmount} onChange={this.handleChangeAmount}/>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <Button className="btn-pay border-0 btn-block py-3" variant="primary" onClick={this.handleQuickPayments}>
                        PAY
                    </Button>
                </Modal.Footer>
            </Modal>

        </header>
    };

}

const mapStateToProps = (state) => {
    return {
        channels: state.channelReducer.channels,
        channelsChanged: state.channelReducer.channelsChanged,
        searchResults: state.searchReducer.results,
        value: state.searchReducer.value,
        suggestions: state.searchReducer.suggestions,
        isLoading: state.searchReducer.isLoading,
        noSuggestions: state.searchReducer.noSuggestions,
        tokens: state.dashboardReducer.tokens,
        tasksCount: state.taskStatusReducer.count,
        tasks: state.taskStatusReducer.tasks
    }
};

const mapDispatchToProps = (dispatch) => {
    const actions = {
        quickPayment: quickPayment,
        onChange:onChange,
        onSuggestionsFetchRequested:onSuggestionsFetchRequested,
        onSuggestionsClearRequested:onSuggestionsClearRequested,
        loadSuggestionsBegin:loadSuggestionsBegin,
        selectedSuggestion: selectedSuggestion,
    };

    return bindActionCreators(actions, dispatch)
};

export default connect(mapStateToProps, mapDispatchToProps)(HeaderContainer);
