import axios from 'axios';
import {
    CLOSE_CHANNELS_SUCCEED,
    DEPOSIT_CHANNELS_SUCCEED,
    POLL_CHANNELS,
    PUT_CHANNELS_SUCCEED,
    TASK_COMPLETE, TASK_FAILED
} from "./types";
import {fromWei, toWei} from "../lib/amounts/weiConversion";
import {getDecimals} from "../lib/tokens/tokensLogic";
import {toast} from "react-toastify";
import {SUCCESS_MSG_TOAST, FAIL_MSG_TOAST, NEW_CHANNEL_OPEN} from "../lib/toast/toastConstants";
import {decrementPendingTask} from "./actionUtils";

export const pollChannels = () =>
    (dispatch, getState) =>
        new Promise((resolve, reject) =>
            axios.get("http://localhost:5001/api/v1/channels").then(response => {
                //console.log(JSON.stringify(response.data.length))
                return resolve(dispatch(pollSucceed(response.data, checkChannelsChanged(getState().channelReducer.channels, response.data))));
            })
                .catch(error => {
                    console.log(JSON.stringify(error));
                })
        );

export const putChannel = (partnerAddress, tokenAddress, totalDeposit, settleTimeOut) => {
    return (dispatch, getState) => {
        return new Promise((resolve, reject) =>
            axios.put(
                "http://localhost:5001/api/v1/channels",
                {
                    "partner_address": partnerAddress,
                    "token_address": tokenAddress,
                    "total_deposit": toWei(totalDeposit, getDecimals(tokenAddress, getState().tokenReducer.tokens)),
                    "settle_timeout": settleTimeOut
                }
            ).then(response => {
                toast.success(SUCCESS_MSG_TOAST);
                return resolve(dispatch(decrementPendingTask(TASK_COMPLETE)));
            })
                .catch(error => {
                    console.log(JSON.stringify(error));
                    toast.error(FAIL_MSG_TOAST);
                    return resolve(dispatch(decrementPendingTask(TASK_FAILED)));

                })
        );
    }

}


export const closeChannel = (tokenAddress, partnerAddress) =>
    (dispatch, getState) =>
        new Promise((resolve, reject) =>
            axios.patch(
                "http://localhost:5001/api/v1/channels/" + tokenAddress + '/' + partnerAddress,
                {
                    "state": "closed"
                }
            )
                .then(response => {
                    toast.success(SUCCESS_MSG_TOAST);
                    return resolve(dispatch(decrementPendingTask(TASK_COMPLETE)));
                    //return resolve(dispatch(closeSucceed(response.data)));
                })
                .catch(error => {
                    console.log(JSON.stringify(error));
                    toast.error(FAIL_MSG_TOAST)
                    return resolve(dispatch(decrementPendingTask(TASK_FAILED)));
                })
        );

export const depositChannel = (tokenAddress, partnerAddress, totalDeposit, balance) =>
    (dispatch, getState) =>
        new Promise((resolve, reject) =>
            axios.patch(
                "http://localhost:5001/api/v1/channels/" + tokenAddress + '/' + partnerAddress,
                {
                    "total_deposit": (Number(toWei(totalDeposit, getDecimals(tokenAddress, getState().tokenReducer.tokens))) + Number(balance))
                }
            )
                .then(response => {
                    toast.success(SUCCESS_MSG_TOAST);
                    return resolve(dispatch(depositSucceed(response.data)));
                })
                .catch(error => {
                    console.log(JSON.stringify(error));
                    toast.error(FAIL_MSG_TOAST);
                })
        );

const pollSucceed = (channels, dataChanged) => (
    {
        type: POLL_CHANNELS,
        data: {channels: channels, channelsChanged: dataChanged}
    }
);

const closeSucceed = (channel) => (
    {
        type: CLOSE_CHANNELS_SUCCEED,
        data: {
            channel: channel
        }
    }
);

const depositSucceed = (channel) => (
    {
        type: DEPOSIT_CHANNELS_SUCCEED,
        data: {
            channel: channel
        }
    }
);

const checkChannelsChanged = (prevStateChannels, newStateChannels) => {
    if (prevStateChannels.length !== newStateChannels.length) {
        toast.success(NEW_CHANNEL_OPEN);
        return true;
    } else {
        //console.log("NO cambiaron")
        return false;
    }
}
