import React, {Component} from "react";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";
import {getPayments} from "../../actions/paymentsActions";
import PollingContainer from "../../genericContainers/PollingContainer";
import {getChannel} from "../../services/channelServices";
import {USER_ADDRESS} from "../../config/applicationConstants";
import {getDecimals} from "../../lib/tokens/tokensLogic";
import {fromWei} from "../../lib/amounts/weiConversion";
import PaymentTable from "../../components/tables/PaymentTable";
import {tableColumns} from "../../apiModels/tableModelPayments";
import {DatePicker} from "@y0c/react-datepicker";
import {ALL_STATUSES} from "../../constants/uiConstants";

class ChannelDetailContainer extends Component{
    async componentDidMount(){
        const actualDate = new Date();
        let oneMonthBefore= new Date().setMonth(actualDate.getMonth()-1);
        const oneMonthBeforeDate = new Date(oneMonthBefore);
        let channelData = await getChannel(this.props.selectedSuggestion.tokenAddress, this.props.selectedSuggestion.partnerAddress);
        this.setState({filterInitiator: process.env.REACT_APP_NODE_ADDRESS,
            filterTarget: channelData.partner_address,
            filterFromDate: oneMonthBeforeDate,
            filterToDate: new Date(),
            myBalance: channelData.balance,
            filterStatus: undefined,
        })

    }

    onStatusChange = (event) => {
        this.setState({filterStatus:event.target.value})
    };

    onFromDateChange(date) {
        this.setState({
            filterFromDate: new Date(date) // The DatePicker lib returns a string.
        });
    };

    onToDateChange(date) {
        this.setState({
            filterToDate:  new Date(date) // The DatePicker lib returns a string.
        });
    };

    applyFilters= async ()=>{
        await this.getData();
    };


    constructor(props) {
        super(props);
        this.state = {
            filterInitiator : undefined,
            filterFromDate: undefined,
            filterToDate: undefined,
            filterStatus: undefined,
            myBalance: undefined,
            loading: true
        };
    }
    render=()=>{
        return <PollingContainer
            render={this.renderPolling}
            pollAction={this.getData}
            dueTim={0}
            periodOfScheduler={2000}
        />
    };

    getData = ()=>{
        console.log(this.state.filterStatus);
        this.props.getPayments(this.state.filterInitiator, this.state.filterTarget, this.state.filterFromDate, this.state.filterToDate, this.state.filterStatus, this.props.selectedSuggestion.networkId)
    };
    renderPolling=()=> {
                let mainTable = null;
                if (this.props.payments && this.state.loading) {
                    mainTable = <PaymentTable data={this.props.payments} columns={tableColumns} defaultPageSize={5} />
                }
                let balanceInTokens = fromWei(this.state.myBalance, getDecimals(this.props.selectedSuggestion.tokenAddress, this.props.tokens));
                return <div className="container-fluid py-5 px-0 my-5">
                    <div className="row mb-5 no-gutters">
                        <div className="col-10 mx-auto">
                            <hr className="position-absolute channel-line" />
                            <div className="row no-gutters align-items-center">
                                <div className="col-auto text-center">
                                    <div className="channel-circle-item d-inline-block shadow-sm position-relative text-center">
                                        <i className="fal fa-2x align-middle fa-fw fa-address-card text-white"></i>
                                        <div className="channel-balance-item bg-white position-absolute rounded px-2 py-1 shadow-sm">{balanceInTokens}</div>
                                    </div>
                                    <p className="text-green">{USER_ADDRESS}</p>
                                </div>
                                <div className="col">
                                    <p className="text-blue text-center pb-5 mb-4">
                                        <i className="fal fa-coins fa-lg mr-2"></i>
                                        {this.props.selectedSuggestion.tokenAddress}
                                    </p>
                                </div>
                                <div className="col-auto text-center">
                                    <div className="channel-circle-item d-inline-block shadow-sm position-relative">
                                        <i className="fal fa-2x align-middle fa-fw fa-address-card text-white"></i>
                                        {/*<div className="channel-balance-item right-balance bg-white position-absolute rounded px-2 py-1 shadow-sm ">300</div>*/}
                                    </div>
                                    <p className="text-green">{this.props.selectedSuggestion.partnerAddress}</p>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="row no-gutters justify-content-center mt-5">
                        <div className="col-10">
                            <div className="card">
                                <h3 className="bg-white text-center h4 m-0 py-3">Channel Payments</h3>
                                <div className="py-2 px-5 filters-bar row mx-0">
                                    <div className="col-2">
                                        <DatePicker onChange={this.onFromDateChange.bind(this)}  dateFormat="YYYY/MM/DD" initialDate={this.state.filterFromDate} selected={this.state.filterFromDate}/>
                                    </div>
                                    <div className="col-2">
                                        <DatePicker onChange={this.onToDateChange.bind(this)}  dateFormat="YYYY/MM/DD" selected={this.state.filterToDate}/>
                                    </div>
                                    <div className="col-auto">
                                        <select className="custom-select bg-transparent" onChange={this.onStatusChange}>
                                            <option value={ALL_STATUSES} defaultValue>All </option>
                                            <option value="1">Received</option>
                                            <option value="2">Failed</option>
                                            <option value="3">Sent</option>
                                        </select>
                                    </div>
                                    <div className="col-auto ml-auto">
                                        <button className="btn btn-green" onClick={async ()=>await this.applyFilters()}>Apply <i className="fal fa-sliders-v ml-3"></i></button>
                                    </div>
                                </div>
                            </div>

                            {mainTable}
                        </div>
                    </div>
                </div>

                    }
}

const mapStateToProps = (state) => {
    return {
        selectedSuggestion: state.searchReducer.suggestion.value,
        payments: state.paymentReducer.payments,
        tokens: state.dashboardReducer.tokens,

    }
};

const mapDispatchToProps = (dispatch) => {
    const actions = {
        getPayments: getPayments
    };
    return bindActionCreators(actions, dispatch)
};

export default connect(mapStateToProps, mapDispatchToProps)(ChannelDetailContainer);
